@import url("https://fonts.googleapis.com/css?family=Varela+Round");
:root {
  --color-1: #21d4fd;
  --color-2: #b721ff;
  --color-3: #08aeea;
  --color-4: #2af598;
}
body {
  font-family: 'Varela Round', sans-serif;
}
.Header {
  position: relative;
  height: 80vh;
  display: block;
}
.Header-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: -webkit-linear-gradient(71deg, var(--color-4), var(--color-1));
  background: -o-linear-gradient(71deg, var(--color-4), var(--color-1));
  background: linear-gradient(19deg, var(--color-4), var(--color-1));
  -webkit-transform-origin: 0px 0px;
          -ms-transform-origin: 0px 0px;
      transform-origin: 0px 0px;
  -webkit-transform: skewY(-10deg);
          -ms-transform: skewY(-10deg);
      transform: skewY(-10deg);
  overflow: hidden;
  z-index: -1;
}
.Header-content {
  text-align: center;
  padding: 4rem 4rem;
  margin: 0 auto;
}
@media (min-width: 58rem) {
  .Header-content {
    text-align: left;
    padding: 10rem 4rem;
    max-width: 54rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
}
.Header-hero h1,
.Header-hero p {
  color: #fff;
  text-shadow: 0 0.5rem 1rem rgba(50,0,100,0.1);
}
.Header-hero h1 {
  margin: 0;
  font-size: 3rem;
}
.Header-hero p {
  font-size: 1.5rem;
  margin-bottom: 2rem;
}
.Header-visuals {
  position: relative;
  margin-top: 5rem;
  -webkit-transform: rotate(60deg);
          -ms-transform: rotate(60deg);
      transform: rotate(60deg);
}
@media (min-width: 58rem) {
  .Header-visuals {
    margin: 0;
    -webkit-transform: translateY(-5rem) rotate(-10deg);
            -ms-transform: translateY(-5rem) rotate(20deg);
        transform: translateY(-5rem) rotate(20deg);
  }
}
.Button {
  padding: 1.5rem 2rem;
  border: 0;
  color: var(--color-3);
  font-size: 1.2rem;
  font-weight: bold;
  background: #fff;
  border-radius: 3px;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
  cursor: pointer;
  -webkit-box-shadow: 0 1.75rem 2rem -0.4rem rgba(50,0,100,0.15);
          box-shadow: 0 1.75rem 2rem -0.4rem rgba(50,0,100,0.15);
}
.Button:hover {
  color: var(--color-4);
  -webkit-box-shadow: 0 1.75rem 3rem 0rem rgba(50,0,100,0.1);
          box-shadow: 0 1.75rem 3rem 0rem rgba(50,0,100,0.1);
  -webkit-transform: scale(1.05);
          -ms-transform: scale(1.05);
      transform: scale(1.05);
}
.Button:active {
  -webkit-box-shadow: 0 1.75rem 2.5rem -0.2rem rgba(50,0,100,0.125);
          box-shadow: 0 1.75rem 2.5rem -0.2rem rgba(50,0,100,0.125);
  -webkit-transform: scale(1.025);
          -ms-transform: scale(1.025);
      transform: scale(1.025);
}
.Iphone {
  display: inline-block;
  background: #fff;
  padding: 4rem 0.6rem 0;

  text-align: center;
}
.Iphone::before,
.Iphone::after {
  content: '';
  display: block;
}
.Iphone::before {
  background-color: var(--color-3);
  background-image: -webkit-gradient(linear, left bottom, left top, from(var(--color-3)), to(var(--color-4)));
  background-image: -webkit-linear-gradient(bottom, var(--color-3) 0%, var(--color-4) 100%);
  background-image: -o-linear-gradient(bottom, var(--color-3) 0%, var(--color-4) 100%);
  background-image: linear-gradient(0deg, var(--color-3) 0%, var(--color-4) 100%);
  width: 16rem;
  padding-top: .5%;
}
.Iphone::after {
  display: inline-block;
  margin: 0.6rem;
  padding: 1.4rem;
  border: 1px solid #773232;
  border-radius: 2rem;
  -webkit-box-shadow: inset -0.05rem 0.05rem 0.5rem 0rem rgba(50,0,100,0.05);
          box-shadow: inset -0.05rem 0.05rem 0.5rem 0rem rgba(50,0,100,0.05);
}



